@font-face {
  font-family: "Avenir Next Cyr";
  font-style: normal;
  font-weight: 400;
  src: url("../resources/fonts/AvenirNextCyr-Regular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Avenir Next Cyr";
  font-style: normal;
  font-weight: 700;
  src: url("../resources/fonts/AvenirNextCyr-Bold.woff") format("woff");
  font-display: swap;
}
html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  font-family: var(--font-family);
  font-size: 14px;
  font-weight: 400;
  line-height: 21px;
  color: var(--color-black);
}

img {
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
  color: inherit;
}

ol, ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

p, h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

button {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;
}
.input-reset::-webkit-search-decoration, .input-reset::-webkit-search-cancel-button, .input-reset::-webkit-search-results-button, .input-reset::-webkit-search-results-decoration {
  display: none;
}

.first {
  padding-left: 0;
}
@media (max-width: 1199.98px) {
  .first {
    padding-left: 15px;
  }
}

.last {
  padding-right: 0;
}
@media (max-width: 1199.98px) {
  .last {
    padding-left: 15px;
    padding-right: 15px;
  }
}

.swiper-pagination {
  width: auto !important;
  height: 22px;
  left: 50% !important;
  transform: translateX(-50%);
  background: rgba(0, 0, 0, 0.7);
  border-radius: 50px;
  padding: 0 7px;
}
.swiper-pagination-bullet {
  margin: 0 !important;
  background: #fff;
}
.swiper-pagination-bullet:not(:last-child) {
  margin-right: 6px !important;
}

:root {
  --font-family: "Avenir Next Cyr", sans-serif;
  --color-black: #414140;
  --color-yellow: #FFDA00;
  --color-light-gray: #F5F7F9;
  --color-green: #27AE60;
}

.breadcrumbs-list {
  display: flex;
  margin-top: 32px;
  padding-bottom: 32px;
}
@media (max-width: 991.98px) {
  .breadcrumbs-list {
    white-space: nowrap;
    overflow-x: scroll;
  }
}
.breadcrumbs-item {
  position: relative;
}
.breadcrumbs-item:not(:last-child) {
  margin-right: 8px;
  padding-right: 18px;
}
.breadcrumbs-item:not(:last-child)::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  width: 10px;
  height: 21px;
  background-image: url("../resources/images/icons/arrow-right.svg");
  background-repeat: no-repeat;
}
.breadcrumbs-link {
  font-size: 14px;
  line-height: 21px;
  text-decoration: underline;
}
.breadcrumbs-link_active {
  text-decoration: none;
}
.breadcrumbs-link:hover {
  color: inherit;
}

@media (max-width: 1199.98px) {
  .form {
    max-width: 370px;
    width: 100%;
    margin: 0 auto;
  }
}
@media (max-width: 576.98px) {
  .form {
    max-width: none;
  }
}
.form-title {
  margin-bottom: 16px;
  font-size: 32px;
  font-weight: 700;
  line-height: 45px;
  text-transform: uppercase;
}
.form-delivery {
  position: relative;
  margin-bottom: 24px;
}
.form-delivery::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 48px;
  height: 42px;
  background-image: url("../resources/images/icons/delivery.svg");
  background-repeat: no-repeat;
}
@media (max-width: 768.98px) {
  .form-delivery::before {
    top: initial;
    left: initial;
    bottom: 6px;
    right: 16px;
    width: 40px;
    height: 40px;
    background-size: contain;
  }
}
.form-delivery__inner {
  width: 342px;
  margin-left: auto;
  padding: 13px 0 14px;
  background: var(--color-light-gray);
}
@media (max-width: 768.98px) {
  .form-delivery__inner {
    width: 100%;
    margin-left: 0;
    padding: 6px 55px 7px 18px;
  }
}
.form-delivery__inner p {
  font-size: 14px;
  line-height: 21px;
  text-align: center;
}
.form-btn {
  margin-bottom: 24px;
}
.form-btn:last-child {
  margin-bottom: 32px;
}
.form-dops {
  margin-bottom: 8px;
  border: 1px solid rgba(65, 65, 64, 0.1019607843);
}
@media (max-width: 576.98px) {
  .form-dops {
    border: none;
    border-top: 1px solid rgba(65, 65, 64, 0.1019607843);
    border-bottom: 1px solid rgba(65, 65, 64, 0.1019607843);
  }
}
.form-dops__remark {
  margin-bottom: 8px;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
  text-align: right;
}
.form-dop {
  display: flex;
  align-items: center;
  padding: 13px 11px;
}
.form-dop:nth-child(2n) {
  background: var(--color-light-gray);
}
.form-dop__index {
  width: 21px;
  height: 21px;
  background: var(--color-yellow);
  border-radius: 50%;
  text-align: center;
}
.form-dop__index span {
  font-size: 11px;
  font-weight: 700;
  line-height: 17px;
  text-align: center;
}
.form-dop__name {
  width: 150px;
  margin-left: 8px;
  font-size: 14px;
  font-weight: 400;
  line-height: 21px;
}
.form-dop__price {
  margin-left: 8px;
  font-size: 14px;
  font-weight: 700;
  line-height: 21px;
}
.form-dop__count {
  max-width: 46px;
  margin-left: auto;
}
.form-price {
  margin-bottom: 24px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.form-price__total {
  font-size: 14px;
  font-weight: 400;
  line-height: 21px;
}
.form-price__total span {
  display: block;
  font-size: 28px;
  font-weight: 700;
  line-height: 42px;
}
.form-price__link {
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
  text-decoration: underline;
}
.form-price__link:hover {
  color: inherit;
}
.form-price__none-quantity {
  max-width: 200px;
  padding: 11px 8px 11px 14px;
  background: linear-gradient(0deg, rgba(252, 63, 29, 0.16), rgba(252, 63, 29, 0.16)), linear-gradient(0deg, rgba(252, 63, 29, 0.04), rgba(252, 63, 29, 0.04));
  border: 1px solid rgba(252, 63, 29, 0.1607843137);
}
.form-price__none-quantity p {
  position: relative;
  padding-right: 29px;
  font-size: 10px;
  font-weight: 450;
  line-height: 13px;
}
.form-price__none-quantity p::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  width: 24px;
  height: 24px;
  background-image: url("../resources/images/icons/arrow-up.svg");
  background-repeat: no-repeat;
}
.form-feedback p {
  position: relative;
  margin-bottom: 4px;
  padding-left: 25px;
  font-size: 16px;
  font-weight: 400;
  line-height: 17px;
}
.form-feedback p::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 17px;
  height: 17px;
  background-image: url("../resources/images/icons/question.svg");
  background-repeat: no-repeat;
}
.form-feedback a {
  padding-left: 25px;
  font-size: 24px;
  font-weight: 700;
  line-height: 36px;
  text-decoration: underline;
}
.form-feedback a:hover {
  color: inherit;
}

.table {
  margin-bottom: 0;
  color: var(--color-black);
}
.table tr:nth-child(2n) {
  background: var(--color-light-gray);
}
.table tr th {
  border: none;
  padding: 9px 0 10px;
  font-size: 14px;
  font-weight: 400;
  line-height: 21px;
}
.table tr th:nth-child(1n) {
  padding-left: 32px;
}
@media (max-width: 576.98px) {
  .table tr th:nth-child(1n) {
    padding-left: 20px;
  }
}
.fancybox-content {
  padding: 32px 32px 25px;
}
@media (max-width: 576.98px) {
  .fancybox-content {
    padding: 32px 24px;
  }
}

.dialog {
  max-width: 370px;
  width: 100%;
}
@media (max-width: 576.98px) {
  .dialog {
    max-width: 335px;
  }
}
.dialog-title {
  margin-bottom: 4px;
  font-size: 28px;
  font-weight: 700;
  line-height: 31px;
}
.dialog-subtitle {
  margin-bottom: 24px;
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
}
.dialog-form__input {
  margin-bottom: 8px;
}
.dialog-form__btn {
  margin-bottom: 18px;
}
.dialog-privacy {
  font-size: 11px;
  font-weight: 400;
  line-height: 17px;
}
.dialog-privacy a {
  text-decoration: underline;
}
.dialog-privacy a:hover {
  color: inherit;
}

.custom-btn {
  width: 100%;
  display: block;
  padding: 18px 0;
  background: var(--color-yellow);
  font-size: 16px;
  font-weight: 700;
  line-height: 20px;
  text-align: center;
  transition: all 0.2s ease-in-out;
}
.custom-btn:hover {
  background: #ecc209;
  color: inherit;
  text-decoration: none;
}
.custom-btn:active {
  transform: translateY(2px);
}

.input {
  position: relative;
  width: 100%;
  padding: 14px 0 14px 48px;
  border: 1px solid rgba(65, 65, 64, 0.1019607843);
}
.input:focus {
  outline: none;
}

.cabinet {
  padding-bottom: 100px;
}
.cabinet-images {
  margin-bottom: 48px;
}
@media (max-width: 991.98px) {
  .cabinet-images {
    padding: 0;
  }
}
.cabinet-images__item {
  position: relative;
}
.cabinet-images__item:not(:last-child) {
  margin-bottom: 24px;
}
@media (max-width: 1199.98px) {
  .cabinet-images__item {
    max-width: 770px;
    width: 100%;
    margin: 0 auto;
  }
}
.cabinet-images__dot {
  position: absolute;
  width: 21px;
  height: 21px;
  background: var(--color-yellow);
  border-radius: 50%;
  text-align: center;
  cursor: pointer;
  z-index: 10;
}
.cabinet-images__dot span {
  font-size: 11px;
  font-weight: 700;
  line-height: 17px;
}
.cabinet-images__frame {
  position: absolute;
  display: none;
  padding: 12px 23px 14px 16px;
  background: rgba(65, 65, 64, 0.85);
}
.cabinet-images__frame p {
  font-size: 14px;
  font-weight: 400;
  line-height: 21px;
  color: #fff;
}
.cabinet-images__frame b {
  color: var(--color-yellow);
}
.cabinet-images__frame.show {
  display: block;
}
.cabinet-images__in-stock {
  position: absolute;
  top: 10px;
  right: 10px;
  padding: 7px 12px 6px 28px;
  background: #fff;
}
@media (max-width: 991.98px) {
  .cabinet-images__in-stock {
    padding: 5px 7px 4px 18px;
  }
}
.cabinet-images__in-stock::before {
  content: "";
  position: absolute;
  top: 10px;
  left: 12px;
  width: 8px;
  height: 8px;
  background: var(--color-green);
  border-radius: 50%;
}
@media (max-width: 991.98px) {
  .cabinet-images__in-stock::before {
    top: 7px;
    left: 7px;
    width: 7px;
    height: 7px;
  }
}
.cabinet-images__in-stock p {
  font-size: 12px;
  font-weight: 700;
  line-height: 14px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
}
@media (max-width: 991.98px) {
  .cabinet-images__in-stock p {
    font-size: 10px;
    line-height: 12px;
  }
}
@media (max-width: 576.98px) {
  .cabinet-form {
    padding: 0;
  }
}
.cabinet-form__wrapper {
  padding: 0 15px;
}
@media (max-width: 576.98px) {
  .cabinet-characteristics {
    padding: 0;
  }
}
@media (max-width: 576.98px) {
  .cabinet-characteristics .cabinet-info__title {
    padding-left: 20px;
  }
}
@media (max-width: 1199.98px) {
  .cabinet-info {
    margin-top: 56px;
  }
}
.cabinet-info__title {
  margin-bottom: 24px;
  font-size: 24px;
  font-weight: 700;
  line-height: 29px;
}
.cabinet-info__wrapper {
  position: relative;
  height: 254px;
  overflow-y: hidden;
}
.cabinet-info__wrapper::before {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 30px;
  background: #fff;
  opacity: 0.9;
}
.cabinet-info__wrapper::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: 30px;
  width: 100%;
  height: 30px;
  background: #fff;
  opacity: 0.5;
}
.cabinet-info__wrapper.view {
  height: auto;
  overflow: none;
}
.cabinet-info__wrapper.view::before {
  display: none;
}
.cabinet-info__wrapper.view::after {
  display: none;
}
.cabinet-info__descr {
  font-size: 16px;
  font-weight: 400;
  line-height: 25.6px;
}
.cabinet-info__descr:not(:last-child) {
  margin-bottom: 20px;
}
.cabinet-info__btn {
  border-bottom: 1px dashed var(--color-yellow);
  font-size: 16px;
  font-weight: 400;
  line-height: 26px;
}