.custom-btn {
  width: 100%;
  display: block;
  padding: 18px 0;
  background: var(--color-yellow);
  font-size: 16px;
  font-weight: 700;
  line-height: 20px;
  text-align: center;
  transition: all .2s ease-in-out;

  &:hover {
    background: #ecc209;
    color: inherit;
    text-decoration: none;
  }

  &:active {
    transform: translateY(2px);
  }
}